import React, {PropsWithoutRef} from 'react';
import * as styles from './prozess.module.scss';
// @ts-ignore
import Hopfen from '../../../svgs/hopfen/icon.svg';
// @ts-ignore
import Coding from '../../../svgs/coding.svg';
// @ts-ignore
import Blume from '../../../svgs/blume.svg';
import CtaButton from '../call-to-action/cta-button/cta-button';

interface ProzessProps {
    prozesses: IProzess[];
    action: () => void;
}

interface IProzess {
    uuid: string;
    heading: string;
    description: string;
}

const Prozess = (props: PropsWithoutRef<ProzessProps>) => {
    const iconMap: {[key: string]: any} = {
        hopfen: <Hopfen/>,
        coding: <Coding/>,
        blume: <Blume/>
    };
    const prozessComponents = props.prozesses.map(prozess =>
        <div key={prozess.uuid} className={styles.prozess}>
            {iconMap[prozess.uuid]}
            <h4 className={styles.prozessHeading}>{prozess.heading}</h4>
            <p className={styles.prozessDescription}>{prozess.description}</p>
        </div>
    );

    return (
        <>
        <div className={styles.prozessContainer}>
            {prozessComponents}
        </div>
            <CtaButton call="Klingt gut?" buttonLabel="Servus Sagen" action={props.action}/>
        </>
    );
};

export default Prozess;
