import React, {PropsWithRef, useRef} from 'react';
import * as styles from './call-to-action.module.scss';
import CtaForm from '../../forms/cta-form/cta-form';
import CtaMap from './cta-map/cta-map';
import {MdEmail, MdPhone} from 'react-icons/all';

interface BookProps {
    ref: any;
}

const CallToAction = (props: PropsWithRef<BookProps>) => {
    const handleCtaForm = () => (form: { email: string }) => {/*console.log(form.email);*/};
    return (
        <div ref={props.ref} className={styles.ctaContainer}>
            <div className={styles.cta}>
                <p>To get the manuscript enter your email here:</p>
                <CtaForm onSubmit={handleCtaForm()}/>
            </div>
            <div className={styles.ctaRight}>
                <div className={styles.ctaContact}>
                    <h3>bayerisch.digital.</h3>
                    <span><MdEmail/> <a href="mainto:tugemail.com">infobayerisch,digital</a></span>
                    <span><MdPhone/><p>01578 7877944</p></span>
                    <p>Julius Spokojny Weg 6</p>
                    <p>86153 Augsburg</p>
                </div>
                <div className={styles.ctaMap}>
                    <CtaMap options={null}/>
                </div>
            </div>
        </div>
    );
};

export default CallToAction;
