import React, {PropsWithChildren, PropsWithoutRef} from 'react';
import * as styles from './intro.module.scss';
import {IAuthor} from '../../../templates/models/IAuthor';
import PreviewCompatibleImage from '../../PreviewCompatibleImage';
import CtaButton from '../call-to-action/cta-button/cta-button';

interface IntroProps {
    motto: string;
    authors: IAuthor[];
    description: string;
    action: () => void;
}

const Intro = (props: PropsWithoutRef<IntroProps>) => {
    const authorComponents = props.authors.map(author =>
        <div key={author.name} className={styles.author}>
            <PreviewCompatibleImage imageClass={styles.authorImage} imageInfo={author.image}/>
            <h4>{author.name}</h4>
            <small>{author.title}</small>
        </div>
    );

    return (
        <div className={styles.intro}>
            <div className={styles.motto}>
                <h1><span>{props.motto}</span></h1>
            </div>
            <div className={styles.text}>
                <div className={styles.textContent}>
                    <p>{props.description}</p>
                    <div className={styles.authorContainer}>
                        {authorComponents}
                    </div>
                    <CtaButton call="Sympathisch?" buttonLabel="Servus Sagen" action={props.action}/>
                </div>
            </div>
        </div>
    );
};

export default Intro;
