import React from 'react';
import { Field, InjectedFormProps, reduxForm, WrappedFieldProps } from 'redux-form';
import { MdArrowForward } from 'react-icons/all';
import * as styles from './cta-form.module.scss';

interface RenderFieldProps extends WrappedFieldProps {
  label: string;
  name: string;
  type: string;
}

const validate = (values: { name: string; email: string }) => {
  const errors: { name?: string; email?: string } = {};
  if (!values.email) {
    errors.email = 'Please enter an email address';
  } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
    errors.email = 'Invalid email address';
  }
    if (!values.name) {
        errors.name = 'Please enter a name';
    }
  return errors;
};


const renderField = ({
                         input,
                         label,
                         type,
                         name,
                         meta: { touched, error, warning }
                     }: RenderFieldProps) => (
    <div style={{ display: 'flex', flexDirection: 'column', height: '3rem' }}>
        <label style={{ display: 'none' }} htmlFor={name}><small>{label}</small></label>
        <input className={(touched && error) ? 'error' : ''} {...input} placeholder={label} name="name" type={type}/>
        {touched &&
        ((error && <small className="hint error">{error}</small>) ||
            (warning && <small>{warning}</small>))}
    </div>
);

const renderTextArea = ({
                         input,
                         label,
                         type,
                         name,
                         meta: { touched, error, warning }
                     }: RenderFieldProps) => (
    <div style={{ display: 'flex', flexDirection: 'column' }}>
        <label style={{ display: 'none' }} htmlFor={name}><small>{label}</small></label>
        <textarea className={(touched && error) ? 'error' : ''} {...input} placeholder={label} name="name" cols={50} rows={5}/>
        {touched &&
        ((error && <small className="hint error">{error}</small>) ||
            (warning && <small>{warning}</small>))}
    </div>
);

const CtaForm = (props: InjectedFormProps<{name: string, email: string, notes?: string}>) => {
  const { handleSubmit, pristine, reset, submitting, valid, anyTouched } = props;
  return (
    <form className={styles.form} onSubmit={handleSubmit}>
        <Field className={styles.formField} name="name" type="text" component={renderField} label="Name"/>
        <Field className={styles.formField} name="email" type="email" component={renderField} label="Email"/>
        <Field className={styles.formField} name="notes" component={renderTextArea} label="Wos du mit uns machen magst. Oder was du guad oder ned so findest."/>
      <button className={styles.formButton} type="submit" disabled={anyTouched && ( !valid || submitting)}>
          Servus Sagen!
      </button>
    </form>
  );
};

export default reduxForm({
  form: 'ctaForm', // a unique identifier for this form
  validate // <--- validation function given to redux-form
})(CtaForm);
