import React, {useRef} from 'react';
import {graphql, Link} from 'gatsby';
import _ from 'lodash';

import Layout from '../components/containers/layout/Layout';
import {Image} from '../models/Image';
import Intro from '../components/components/intro/intro';
import SEO from '../components/containers/seo/seo';
import {IndexPageProps, IndexPageTemplateProps} from './models/props/IndexPageProps';
import IndexModule from '../components/components/util/index-module/index-module';
import Prozess from '../components/components/prozess/prozess';
import Trophaensammlung from '../components/components/trophaensammlung/trophaensammlung';
import CallToAction from '../components/components/call-to-action/call-to-action';

export const IndexPageTemplate = ({
                                      image,
                                      title,
                                      heading,
                                      subheading,
                                      mainpitch,
                                      mission,
                                      featuredPost,
                                      prozess,
                                      posts,
                                  }: IndexPageTemplateProps) => {
    const reference = useRef(null);
    const scrollToRef = (ref: any) => ref && ref.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
    const onCtaClicked = () => scrollToRef(reference);
    return (
        <>
            <SEO title={title}/>
            <IndexModule dark={true} heading={mainpitch.heading}>
                <Intro motto={mainpitch.motto} authors={mainpitch.authors} description={mainpitch.description} action={onCtaClicked}/>
            </IndexModule>
            <IndexModule heading="UNSER [BRAU]PROZESS">
                <Prozess prozesses={prozess} action={onCtaClicked}/>
            </IndexModule>
            <IndexModule dark={true} heading="UNSERE TROPHAENSAMMLUNG">
                <Trophaensammlung posts={posts} action={onCtaClicked}/>
            </IndexModule>
            <IndexModule ref={reference} heading="SAG SERVUS">
                <CallToAction/>
            </IndexModule>
        </>
    );
};


const IndexPage = ({data}: IndexPageProps) => {
    const posts = data.allMarkdownRemark.edges;
    const {node: featuredPost} = posts[0];
    const {frontmatter} = data.markdownRemark;

    return (
        <Layout heroImage={true}>
            <IndexPageTemplate
                image={frontmatter.image}
                title={frontmatter.title}
                heading={frontmatter.heading}
                subheading={frontmatter.subheading}
                mainpitch={frontmatter.mainpitch}
                prozess={frontmatter.prozess}
                mission={frontmatter.mission}
                featuredPost={featuredPost}
                posts={posts}
            />
        </Layout>
    );
};

export default IndexPage;

export const pageQuery = graphql`
  query IndexPageTemplate {
    allMarkdownRemark(sort: {order: DESC, fields: frontmatter___featuredpost}, filter: {frontmatter: {featuredpost: {}, templateKey: {eq: "blog-post"}}}) {
        edges {
          node {
            id
            fields {
                slug
            }
            frontmatter {
                date(formatString: "MMMM DD, YYYY")
                title
                description
                tags
                featuredimage {
                    childImageSharp {
                        fluid(maxWidth: 2048, quality: 100) {
                          ...GatsbyImageSharpFluid
                        }
                      }
                }
            }
          }
        }
      }
    markdownRemark(frontmatter: { templateKey: { eq: "index-page" } }) {
      frontmatter {
        title
        image {
          childImageSharp {
            fluid(maxWidth: 2048, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        mainpitch {
          motto
          heading
          description
          authors {
            name
            title
            image {
              childImageSharp {
                fluid(maxWidth: 2048, quality: 100) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
           }
        }
        prozess {
            uuid
            heading
            description
        }
        mission {
          side
          heading
          values {
            text
            image {
              childImageSharp {
                fluid(maxWidth: 2048, quality: 80) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
    }
  }
`;
