import React, { Component } from 'react';
import { Map, GoogleApiWrapper } from 'google-maps-react';

const mapStyles = {
    width: '100%',
    height: '100%'
};

const containerStyle = {
    position: 'relative',
    width: '100%',
    height: '100%',
    minWidth: '350px',
    minHeight: '500px'
};

export class CtaMap extends Component<any> {
    render() {
        return (
            <Map
                containerStyle={containerStyle}
                google={this.props.google}
                zoom={14}
                style={mapStyles}
                initialCenter={{
                    lat: 48.366512,
                    lng: 10.894446
                }}
            />
        );
    }
}

// @ts-ignore
export default GoogleApiWrapper({apiKey: 'AIzaSyCBGe2Qu6G_eINiYN28_igiiifEKRmj8uw'})(CtaMap);
